// import css from './Contacts.module.css'
import { nanoid } from 'nanoid';

const Contacts = ({ contactList }) => {
  return (
    <>
      <ul>
        {contactList.map(contact => {
          return (
            <li key={nanoid()}>
              {contact.name}: {contact.number}
            </li>
          );
        })}
      </ul>
    </>
  );
};

export default Contacts;
